.msges{
  width: 36px;
  height: 36px;
  border-radius: 100%;
  background-repeat: no-repeat;
  background-size:cover;
  background-position: center center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  
}

@media screen and (orientation:landscape){
  .App{-ms-flex-direction: column;flex-direction: column;-ms-flex-pack: justify;justify-content: space-between;}
  .CardFace,.CardBack{width:58%;height: calc(80vw / 1.7)}
  .Controls,.Messages{
    padding-top:24px;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 58%;
    height: 80px;
  }
  .close{
    width:28px;
    height:28px;
    border-radius: 100%;
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center center;
    position:fixed;
    bottom:4px;
    right:4px;
  }
  .langChange{
    position:absolute;
    top:20px;
    left:32px;
  }
  .msgesText{
    padding-top: 24px;
    text-align: center;
  }
  
}
@media screen and (orientation:portrait){
  .App{-ms-flex-direction: column;flex-direction: column;-ms-flex-pack: center;justify-content: center;-ms-flex-align: center;align-items: center;margin-top:-70px;}
  .CardFace,.CardBack{width:80%;height: calc(90vw / 2.05);margin-top:70%;}
  .Controls,.Messages{
    -ms-flex-direction: row;
        flex-direction: row;
    width: 80%;
    height: 80px;
    bottom:4px;
  }
  .msgesText{
    padding-top: 42px;
    text-align: center;
  }
  .langChange{
    position:absolute;
    top:120px;
    left:48px;
  }
  .close{
    width:28px;
    height:28px;
    border-radius: 100%;
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center center;
    position:fixed;
    bottom:4px;
    right:4px;
  }
 
}


